//import {LocalDataSource} from 'ng2-smart-table/ng2-smart-table/lib';
import { Component, ViewChild } from "@angular/core";
import { BaModalComponent } from '../../../../../theme/components/baModal/baModal.component';
import { LocalDataSource } from "../../../../../ng2-smart-table/ng2-smart-table";
import "style-loader!./../smartTables.scss";
import { Router } from "@angular/router";
import { AuthService } from "../../../../../common/services/permission/authService.service";
import { AddUserService } from "../adduser.service";
import { MasterRepo } from "../../../../../common/repositories/masterRepo.service";
@Component({
  selector: "role-list",
  templateUrl: "./roleList.component.html",
  providers: [AddUserService],
})
export class RoleList {
  @ViewChild("childModal") childModal: BaModalComponent;
  @ViewChild("deleteModal") deleteModal: any;
  DialogMessage: string = "You are not authorized";
  selectedData: any;
  settings = {
    mode: "external",
    edit:null,
    delete:null,
    columns: {
      role: {
        title: "Role",
        type: "text",
      },
    },
  };
  source: LocalDataSource = new LocalDataSource();

  constructor(
    private adduserService: AddUserService,
    private router: Router,
    private _authService: AuthService,
    private service: MasterRepo
  ) {
    this.loadUserRoles();   
    this.settings.delete = this.menuRight.delete ? {} : null;
    this.settings.edit = this.menuRight.edit ? {} : null;

}

get disableAdd(){
  return !this.menuRight.create;
}

get menuRight(){
  var right =  this._authService.getRole.menuRights.find(x=>x.menuId === 'roles')
  return right || {edit:true}
}

  loadUserRoles() {
    let data: Array<any> = [];
    this.adduserService.getRoleNames().subscribe(
      (res:any) => {
        res
          .map((x) => ({ role: x }))
          .forEach((element) => {
            data.push(element);
          });
        this.source.load(data);
        console.log(data);
      },
      (error) => {
        this.service.handleWebError(error);
      }
    );
  }

  onDeleteConfirm(event): void {
    try {
      this.selectedData = event.data;
      this.deleteModal.show();
    } catch (e) {
      alert(e);
    }
  }
  DeleteEvent() {
    try {
      let result = [];
      this.deleteModal.hide();
      this.DialogMessage = "Deleting please wait...";
      this.childModal.show();
      let sub = this.adduserService
        .saveRole({ mode: "delete", data: this.selectedData })
        .subscribe(
          (response: any) => {
            var result = response;
            if (result.status == "ok") {
              this.DialogMessage = "Role deleted sucessfully.";
              this.loadUserRoles();
              setTimeout(() => {
                this.childModal.hide();
              }, 1000);
            } else {
              console.log({ response: response, result: result });
              this.DialogMessage = "Role could not be saved.";
              setTimeout(() => {
                this.childModal.hide();
              }, 1000);
            }
          },
          (error) => {
            this.service.handleWebError(error);
            this.childModal.hide();
          }
        );
    } catch (e) {
      alert(e);
    }
  }
  hideChildModal() {
    this.childModal.hide();
  }
  onEditClick(event): void {
    this.router.navigate([
      "pages/userManager/addRole",
      { role: event.data.role, returnUrl: this.router.url },
    ]);
  }
}
