import {AddUser} from './components/userManger/adduser.component';
import {Routes,RouterModule,CanActivate} from '@angular/router';
import {UserManager} from './userManager.component';
import {CanActivateTeam} from '../../common/services/permission/guard.service';
import {UserList} from './components/userManger/userList.component';
import { RoleList } from './components/userManger/roles/roleList.component';
import { AddRole } from './components/userManger/roles/addRole.component';
const routes:Routes =[
    {
        path:'',
        component: UserManager,
        children:[
            
             {path:'userlist',component:UserList},
             {path:'adduser',component:AddUser},
             {path:'roles',component:RoleList},
             {path:'addRole',component:AddRole}
        ]
        
    }
];

export const routing = RouterModule.forChild(routes);