import { Component, OnInit, ViewChild } from "@angular/core";
import { AddUserService } from "./../adduser.service";
import { ActivatedRoute, Router } from "@angular/router";
import { MasterRepo } from "../../../../../common/repositories";
import { PAGES_MENU } from "./../../../../pages.menu";
import { BaModalComponent } from '../../../../../theme/components/baModal/baModal.component';
import { CommonService } from "../../../../../common/services/common.service";
@Component({
  selector: "role-form",
  templateUrl: "./addRole.component.html",
  styleUrls: ["./../modals.scss"],
  providers: [AddUserService],
})
export class AddRole implements OnInit {
  @ViewChild("childModal") childModal: BaModalComponent;
  modeTitle: string = "Add Role";
  DialogMessage: string = "Saving data please wait ...";
  mode: string = "add"; //mode of the form add or edit
  menuRights: any[] = [];
  role: string;

  constructor(
    private addUserService: AddUserService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private service: MasterRepo,
    private common: CommonService
  ) {
    this.role = this.activatedRoute.snapshot.params["role"] || "";
    if (this.role && this.role !== "") {
      this.mode = "edit";
      this.modeTitle = "Edit Role";
    } else {
      this.mode = "add";
      this.modeTitle = "Add Role";
    }
  }

  ngOnInit() {
    this.common.recursiveForEach(PAGES_MENU, (item) => {
      if (
        ["pages", "treatment-list", "treatment-details","series","seriesSales","disease","medication","prescription"].indexOf(item.path) !==
        -1
      )
        return;
      var menu = item.data && item.data.menu;
      this.menuRights.push({
        menuId: item.path,
        menuName: menu && menu.title,
        allowCreate: this.common.coalesce(menu && menu.allowCreate, true),
        allowEdit: this.common.coalesce(menu && menu.allowEdit, true),
        allowDelete: this.common.coalesce(menu && menu.allowDelete, true),
        create: this.common.coalesce(menu && menu.allowCreate, true),
        edit: this.common.coalesce(menu && menu.allowEdit, true),
        delete: this.common.coalesce(menu && menu.allowDelete, true),
      });
    });

    if (this.mode === "edit") {
      this.loadUserRights();
    }
  }

  loadUserRights() {
    this.addUserService.getUserRole(this.role).subscribe(
      (response: any) => {
        
        this.menuRights.forEach((mRight) => {
          var userRight = response.menuRights.find(
            (menuRight) => menuRight.menuId === mRight.menuId
          );
          if(userRight){
            mRight.hidden = userRight.hidden;
            mRight.create = userRight.create;
            mRight.edit = userRight.edit;
            mRight.delete = userRight.delete;
          }else{
            mRight.hidden = true;
            mRight.create = false;
            mRight.edit = false;
            mRight.delete = false;
          }
          
        });
      },
      (error) => {
        this.service.handleWebError(error);
      }
    );
  }

  back() {
    this.router.navigate(["/pages/userManager/roleList"]);
  }

  onSubmit() {
    this.childModal.show();
    this.DialogMessage = "Saving data please wait ...";
    let data = {
      mode: this.mode,
      data: {
        role: this.role,
        menuRights: this.menuRights.map((x) => ({
          menuId: x.menuId,
          menuName: x.menuName,
          hidden: x.hidden || false,
          create: x.create,
          edit: x.edit,
          delete: x.delete,
        })),
      },
    };
    if (data) {
      this.addUserService.saveRole(data).subscribe(
        (response: any) => {
          var result = response;
          if (result.status == "ok") {
            this.DialogMessage = "Role saved sucessfully.";
            setTimeout(() => {
              this.childModal.hide();
              this.router.navigate(["/pages/userManager/roleList"]);
            }, 1000);
          } else {
            console.log({ response: response, result: result });
            this.DialogMessage = "Role could not be saved.";
            setTimeout(() => {
              this.childModal.hide();
            }, 1000);
          }
        },
        (error) => {
          this.service.handleWebError(error);
          this.childModal.hide();
        }
      );
    } else {
      this.DialogMessage = "Invalid data. Please check the entry";
      setTimeout(() => {
        this.childModal.hide();
      }, 3000);
    }
  }
}
