import { Component, OnInit, ViewChild } from '@angular/core';
import { PAGES_MENU } from '../../../../../app/pages/pages.menu';
import { Routes, ActivatedRoute, Router } from '@angular/router';
import { LocalDataSource } from '../../../../ng2-smart-table/ng2-smart-table';
import { FormGroup, FormBuilder, FormControl, Validators, FormArray } from "@angular/forms";
import 'style-loader!./grid.scss';
import { AddUserService } from "./adduser.service";
import { EmailValidator, EditPasswordValidator } from "../../../../theme/validators";
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Login } from '../../../login';
import { MasterRepo } from '../../../../common/repositories';
import { Branch } from '../../../../common/interfaces';
//import {BaCheckbox} from '../../../../theme/components'

@Component({
    selector: 'user-manager',
    templateUrl: './adduser.component.html',
    styleUrls: ['./modals.scss'],
    providers: [AddUserService]
})

export class AddUser implements OnInit {
    @ViewChild('childModal') childModal: BaModalComponent;
    @ViewChild('loginModal') loginModal: any;
    DialogMessage: string = "Saving data please wait ..."
    mode: string = 'add' //mode of the form add or edit
    modeTitile: string = "Add user";
    userForm: FormGroup //main form
    userMenuList = []; //list data menulist of user
    items = []; //converted array of usermenu from main menu
    userRightList = [] //list of rights of user or empty {right,description,value,valutype}
    userType: string = "group";//default user type ie, usergroup or user
    branchList: Branch[] = []; 
    dropdownSettings = {};
    selectedBranches: Branch[] = [];
    roles:string[] = [];
    private returnUrl: string;
    private user: string;
    //source: LocalDataSource = new LocalDataSource();
    constructor(private fb: FormBuilder
        , private addUserService: AddUserService
        , private router: Router
        , private activatedRoute: ActivatedRoute
        , private service: MasterRepo
    ) {
        //getting parameter passed
        this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'] || '';
        this.user = this.activatedRoute.snapshot.params['user'] || '';

        if (this.user && this.user !== '') {
            this.mode = 'edit'; this.modeTitile = "Editing User";
        }
        else {
            this.mode = 'add'; this.modeTitile = "Add User";
        }
    }

    ngOnInit() {
        this.service.getBranchList().subscribe((response: Branch[]) => {
            this.branchList = response;
        },(err)=>{
            this.service.handleWebError(err);
        });

        this.addUserService.getRoleNames().subscribe((data) => {
            this.roles = <string[]>data;
        },(error) => {
            this.service.handleWebError(error);
        });
        
        this.userForm = this.fb.group({
            UserId: ['', [Validators.required]],
            Name: ['', [Validators.required]],
            Password: ['', Validators.required],
            email: [''],
            Role: ['', Validators.required],
            address: [''],
            mobile: ['']
            , branches:[[]]
            , defaultBranch: ['', Validators.required]
        })
        if (this.mode == "edit") {
            this.addUserService.getUserProfile(this.user)
                .subscribe(data => {                   
                    this.userForm.patchValue({
                      UserId: data.UserId,
                      Name: data.Name,
                      email: data.email,
                      Role: data.Role,
                      address: data.address,
                      mobile: data.mobile,
                      defaultBranch: data.defaultBranch,
                    });                   
                    this.selectedBranches = data.branches;            
                })
        }
        
        this.dropdownSettings = {
            enableCheckAll: true,
            text: 'Select Outlets',
            enableSearchFilter: true,
            labelKey:"branchName",
            primaryKey:"branchId"
        };
          
    }




    saveData($event) {
        try {
            this.addUserService.saveUser($event)
                .subscribe((data : any) => {
                    if (data.status == 'ok') {
                        this.DialogMessage = "User saved sucessfully"
                        setTimeout(() => {
                            this.childModal.hide();
                            this.router.navigate([this.returnUrl]);
                        }, 1000)
                    }
                    else {
                        if (data.result._body == "The ConnectionString property has not been initialized.") {
                            this.loginModal.show();
                            //this.router.navigate(['/login', this.router.url])
                            return;
                        }
                        //Some other issues need to check
                        this.DialogMessage = "Error in Saving Data:" + data.result._body;
                        //console.log(data.result._body);
                        setTimeout(() => {
                            this.childModal.hide();
                        }, 4000)
                    }
                }, Error => {
                    try {
                        let t = Error.json();
                        //console.log(t);
                        this.DialogMessage = t.result + "Please check and try again";
                        //let p = t.json();
                        if (t.result == "The ConnectionString property has not been initialized.") {
                            //this.DialogMessage="erro in saving. " +Error._body;
                            setTimeout(() => {
                                this.childModal.hide();
                                this.loginModal.show();
                            }, 3000)
                        }
                        setTimeout(() => {
                            this.childModal.hide();

                        }, 2000)

                    }
                    catch (e) {
                        this.DialogMessage = e
                        setTimeout(() => {
                            this.childModal.hide();

                        }, 3000)
                    }
                })
        }
        catch (ex) {
            alert(ex);
            this.DialogMessage = ex;
            setTimeout(() => {
                this.childModal.hide();
            }, 3000)
        }
    }
    ngOnChanges() {
        //console.log(this.userForm);
    }
    onSubmit() {
        this.childModal.show();
        this.DialogMessage = "Saving data please wait ...";
        let data = this.prepareToSave();
        if (data) {
            this.saveData(data);
        }
        else {
            this.DialogMessage = "Invalid data. Please check the entry";
            setTimeout(() => {
                this.childModal.hide();
            }, 3000)
        }
    }
    prepareToSave() {
        if (this.userForm.controls['UserId'].status == 'INVALID' ||
            (this.mode == "add" && this.userForm.controls['Password'].status == 'INVALID') ||
            this.userForm.controls['Role'].status == 'INVALID'
        ) {
            return null;
        }
        this.userForm.patchValue({branches:this.selectedBranches});
        if (this.mode == 'edit')
        {  return { mode: 'edit', data: this.userForm.value }; }
        else
            return { mode: 'add', data: this.userForm.value };
        //console.log(JSON.stringify(this.userForm.value.menuRights));
    }

    hideChildModal() {
        this.childModal.hide();
    }
    hideloginModal() {
        this.loginModal.hide();
    }
    back() {
        //console.log(this.userForm);

        //this.childModal.show();
        //this.loginModal.show();
        this.router.navigate([this.returnUrl]);
    }
}

